@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,700&display=swap);
.menu {
  display: -ms-flexbox;
  width: 26px;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}
.menu > span {
  height: 3px;
  border-radius: 3px;
  background-color: #212121;
  margin-bottom: 6px;
  display: block;
  transition: all 0.25s ease-in-out;
}
.menu > span:first-child {
  width: 13px;
}
.menu > span:nth-child(2) {
  width: 26px;
}
.menu > span:nth-child(3) {
  width: 18px;
}
.menu:hover > span:first-child {
  width: 26px;
}
.menu:hover > span:nth-child(2) {
  width: 18px;
}
.menu:hover > span:nth-child(3) {
  width: 13px;
}

.list-group-item {
  padding: 1.5rem 1.5rem !important;
}
.active:hover {
  background-color: #0b5ed7 !important;
  border-color: #0a58ca !important;
}
a > img {
  width: 10px;
  height: 20px;
}
div.d-flex > img {
  width: 100px;
  height: 100px;
}

div.container > img {
  width: 100px;
  height: 100px;
}

* {
  font-family: Rubik, sans-serif;
}

body {
  background-image: url(/CodeValue/static/media/background.bcb52716.jpg);
  background-repeat: no-repeat;
  background-position: center center;
